body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.header-top {
  color: #fff;
  background-color: #333;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.header-logo {
  width: 50px;
  height: auto;
}

.searchbox {
  background-color: #fff;
  padding: 10px;
}

.container {
  height: calc(100vh - 100px);
  display: flex;
}

.sidebar {
  box-sizing: border-box;
  background-color: #f4f4f4;
  flex-shrink: 0;
  width: 20%;
  padding: 20px;
}

.main-content {
  box-sizing: border-box;
  background-color: #90ee90;
  flex: 1;
  padding: 20px;
  overflow: auto;
}

#hits {
  border: 2px solid red;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.ais-Hits-item {
  background-color: #fff;
  margin: 0;
  padding: 0;
  display: block;
}

.hit {
  box-sizing: border-box;
  background-color: #add8e6;
  border: 1px solid #ddd;
  padding: 10px;
}

.hit-image {
  width: 100%;
  height: auto;
}

#pagination {
  margin-top: 20px;
}
